<template>
  <div id = 'dashboard'>
      <ul class="collection with-header"> 
        <li class="collection-header"><h4>Dashboard</h4>   </li>
      </ul>
      <div id="crypto-container" v-for="(value, key) in cryptos">
      <span class="left">{{ key }}</span>
      <span class="right">${{ value.USD }}</span>
      </div>
     
    
      

      <div class="fixed-action-btn">
        <router-link
           to="/" 
           class="btn-floating btn-large red">
           <i class="fa fa-plus"></i>
        </router-link>
      </div>
     
  </div>
  
</template>

<script>
//import db from './firebaseinit'
import axios from 'axios'

export default {
  name: 'dashboard',
  data: () => ({
     cryptos:[],
     errors: []
   }),

  
  created() {
    axios.get('https://min-api.cryptocompare.com/data/pricemulti?fsyms=BTC,LTC,ETH,IOT,XLM,XRP,DASH&tsyms=USD,XMR&extraParams=your_app_name')
    .then(response => {
       this.cryptos = response.data
       console.log(response)
    })
    .catch(e => {
        this.errors.push(e)
    })
    
  }

}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
  body {
    background: #f1f1f1;
  }

  div#crypto-container {
    background:rgb(255, 255, 255);
    width: 80%;
    margin: 0 auto 4px auto;
    padding: 1.5em;
    box-shadow: 1px 1px 0 lightgrey;
  }

  span.left {
    font-weight: bold;
  
  }

  span.right {
    float:right;
  }
</style>



